@import "../../../variables";

.ms-accordion {
	&-body {

		background-color: $white;
		max-height: 0; 
		overflow: hidden;
		transition: max-height 1s ease;
	
		&.active {
			max-height: 1000px;
			transition: max-height 1s ease;
		}
	
		&-content {
			font-size: 1.2rem;
			padding: 10px;
		}
	}
	
}
